import React, {memo, useMemo} from 'react'
import styled, {keyframes, css} from 'styled-components'
import logo from './logo.svg'
import gopher from './gopher-seeklogo.com.svg'
import nodeJs from './Node.js_logo.svg'
import duke from './duke_badge.svg'

const Styling = styled.div`
display: flex;
height: 128px;
`
const RowSize = css`height: 128px`
export default memo(() =>
  <Styling>
    <ReactLogo css={RowSize} />
    <GoLogo css={RowSize} />
    <NodeLogo css={RowSize} />
    <JavaLogo css={RowSize} />
  </Styling>)

const rotate360 = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`
// 6 points: 2 s => 12 s period
const ReactLogo = memo(({css}) => {
  const SpinningReact = useMemo(() => styled.img`animation: ${rotate360} infinite 12s linear;${css}`, [css || true])
  return <SpinningReact src={logo} alt="React logo" />})

const slideUp = keyframes`
{
  0% {
    transform: translateY(256px);
  }
  100% {
    transform: translateY(0);
  }
}`
const Clip = styled.div`overflow: hidden`
const GoLogo = memo(({css}) => {
  const SpinningReact = useMemo(() => styled.img`animation: ${slideUp} ease-in-out 2s infinite;${css}`, [css || true])
  return <Clip><SpinningReact src={gopher} alt="Golang logo" /></Clip>})

const heartbeat = keyframes`
{
  0%, 40%, 80%, 100%
  {
    transform: scale(.9);
  }
  20%, 60%
  {
    transform: scale(1);
  }
}`
const NodeLogo = memo(({css}) => {
  const SpinningReact = useMemo(() => styled.img`animation: ${heartbeat} infinite 2s linear;${css}`, [css || true])
  return <SpinningReact src={nodeJs} alt="Node.js logo" />})

const Bounce = keyframes`
{
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}`
const JavaLogo = memo(({css}) => {
  const JumpingDuke = useMemo(() => styled.img`animation: ${Bounce} ease-in-out 2s infinite;${css}`, [css || true])
  return <JumpingDuke src={duke} alt="Java duke" />})
